// ==========================================
// RISIVO CRM - Power Dialer Calls API
// ==========================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { DialerCall, CallStatus, DialerApiResponse, PaginatedResponse } from '@/lib/power-dialer/types'

// Initialize Supabase client
const supabase = supabaseAdmin

// GET - List calls or get single call
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const callId = searchParams.get('id')
    const campaignId = searchParams.get('campaignId')
    const contactId = searchParams.get('contactId')
    const agentId = searchParams.get('agentId')
    const status = searchParams.get('status') as CallStatus | null
    const dateFrom = searchParams.get('dateFrom')
    const dateTo = searchParams.get('dateTo')
    const page = parseInt(searchParams.get('page') || '1')
    const pageSize = parseInt(searchParams.get('pageSize') || '50')
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    // Single call fetch
    if (callId) {
      const { data: call, error } = await supabase
        .from('DialerCall')
        .select('*')
        .eq('id', callId)
        .eq('subAccountId', subAccountId)
        .single()
      
      if (error) {
        return NextResponse.json({
          success: false,
          error: error.message
        }, { status: 404 })
      }
      
      return NextResponse.json({
        success: true,
        data: call
      })
    }
    
    // List calls with filters
    let query = supabase
      .from('DialerCall')
      .select('*', { count: 'exact' })
      .eq('subAccountId', subAccountId)
      .order('initiatedAt', { ascending: false })
    
    if (campaignId) {
      query = query.eq('campaignId', campaignId)
    }
    
    if (contactId) {
      query = query.eq('contactId', contactId)
    }
    
    if (agentId) {
      query = query.eq('agentId', agentId)
    }
    
    if (status) {
      query = query.eq('status', status)
    }
    
    if (dateFrom) {
      query = query.gte('initiatedAt', dateFrom)
    }
    
    if (dateTo) {
      query = query.lte('initiatedAt', dateTo)
    }
    
    // Pagination
    const from = (page - 1) * pageSize
    const to = from + pageSize - 1
    query = query.range(from, to)
    
    const { data: calls, error, count } = await query
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    const response: PaginatedResponse<DialerCall> = {
      success: true,
      data: calls || [],
      pagination: {
        page,
        pageSize,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / pageSize)
      }
    }
    
    return NextResponse.json(response)
    
  } catch (error) {
    console.error('GET /api/v1/dialer/calls error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// POST - Create a new call record (initiating a call)
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { subAccountId, ...callData } = body
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    if (!callData.fromNumber || !callData.toNumber) {
      return NextResponse.json({
        success: false,
        error: 'fromNumber and toNumber are required'
      }, { status: 400 })
    }
    
    // Create call record
    const { data: call, error } = await supabase
      .from('DialerCall')
      .insert({
        subAccountId,
        campaignId: callData.campaignId || null,
        queueItemId: callData.queueItemId || null,
        contactId: callData.contactId || null,
        direction: callData.direction || 'outbound',
        status: 'initiated',
        fromNumber: callData.fromNumber,
        toNumber: callData.toNumber,
        formattedFrom: callData.formattedFrom || null,
        formattedTo: callData.formattedTo || null,
        agentId: callData.agentId || null,
        agentName: callData.agentName || null,
        scriptId: callData.scriptId || null,
        scriptName: callData.scriptName || null,
        initiatedAt: new Date().toISOString()
      })
      .select()
      .single()
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    // TODO: Integrate with Twilio to actually make the call
    // This is where you would call the Twilio API
    // const twilioCall = await twilioClient.calls.create({...})
    // Then update the call record with callSid
    
    return NextResponse.json({
      success: true,
      data: call,
      message: 'Call initiated successfully'
    }, { status: 201 })
    
  } catch (error) {
    console.error('POST /api/v1/dialer/calls error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// PUT - Update a call record (status, disposition, etc.)
export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()
    const { id, subAccountId, ...updateData } = body
    
    if (!id || !subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'id and subAccountId are required'
      }, { status: 400 })
    }
    
    // Build update object
    const updateObj: Record<string, unknown> = {
      ...updateData,
      updatedAt: new Date().toISOString()
    }
    
    // If status is completed and we have timing, calculate duration
    if (updateData.status === 'completed' && updateData.endedAt) {
      const call = await supabase
        .from('DialerCall')
        .select('initiatedAt, answeredAt')
        .eq('id', id)
        .single()
      
      if (call.data) {
        const endTime = new Date(updateData.endedAt).getTime()
        const initTime = new Date(call.data.initiatedAt).getTime()
        updateObj.totalDuration = Math.floor((endTime - initTime) / 1000)
        
        if (call.data.answeredAt) {
          const answerTime = new Date(call.data.answeredAt).getTime()
          updateObj.ringDuration = Math.floor((answerTime - initTime) / 1000)
          updateObj.talkDuration = Math.floor((endTime - answerTime) / 1000)
        }
      }
    }
    
    // If disposition is set, mark disposition time
    if (updateData.dispositionId) {
      updateObj.dispositionAt = new Date().toISOString()
    }
    
    // Update call
    const { data: call, error } = await supabase
      .from('DialerCall')
      .update(updateObj)
      .eq('id', id)
      .eq('subAccountId', subAccountId)
      .select()
      .single()
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    // If call is completed and has a campaign, update campaign stats
    if (updateData.status === 'completed' && call.campaignId) {
      await updateCampaignStats(call.campaignId)
    }
    
    return NextResponse.json({
      success: true,
      data: call,
      message: 'Call updated successfully'
    })
    
  } catch (error) {
    console.error('PUT /api/v1/dialer/calls error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// Helper function to update campaign stats
async function updateCampaignStats(campaignId: string) {
  try {
    // Get all completed calls for this campaign
    const { data: calls } = await supabase
      .from('DialerCall')
      .select('status, outcome, talkDuration, isConnected')
      .eq('campaignId', campaignId)
    
    if (!calls) return
    
    const stats = {
      totalCalls: calls.length,
      answeredCalls: calls.filter(c => c.isConnected).length,
      voicemails: calls.filter(c => c.outcome === 'voicemail').length,
      noAnswers: calls.filter(c => c.outcome === 'no_answer').length,
      busySignals: calls.filter(c => c.outcome === 'busy').length,
      failedCalls: calls.filter(c => c.outcome === 'failed').length,
      totalTalkTime: calls.reduce((sum, c) => sum + (c.talkDuration || 0), 0),
      avgCallDuration: 0
    }
    
    if (stats.answeredCalls > 0) {
      stats.avgCallDuration = Math.floor(stats.totalTalkTime / stats.answeredCalls)
    }
    
    await supabase
      .from('DialerCampaign')
      .update(stats)
      .eq('id', campaignId)
      
  } catch (error) {
    console.error('Error updating campaign stats:', error)
  }
}
